<template>
    <div class="post-result">
        <h1>投稿結果</h1>
        <div>
            <h3>獲得リア充度</h3>
            <div>
                <table>
                    <tr>
                        <td>合計リア充度:</td><td>{{ result.total_point }}ml</td>
                    </tr>
                </table>
            </div>
            <div>
                <section class="point-section">
                    <h3 class="point-title">投稿リア充度</h3>
                    <div class="point-info">
                        <table>
                            <tr>
                                <td>{{ result.todo.name }}</td><td>+{{ result.todo.base_point }}ml</td>
                            </tr>
                        </table>
                    </div>
                </section>
                <section class="point-section">
                    <h3 class="point-title">獲得リア充ボーナス</h3>
                    <div class="point-info">
                        <table v-for="bonus in result.getBonuses" v-if="result.getBonuses.length != 0">
                            <tr>
                                <td>{{ bonus.name }}</td>
                                <td>+{{ bonus.point }}ml</td>
                            </tr>
                        </table>
                        <div v-else="result.getBonuses.length != 0">
                            獲得したボーナスなし
                        </div>
                        <div>
                            合計ボーナスリア充度: {{ totalBonusPoint }}ml
                        </div>
                    </div>
                </section>
                <section class="point-section">
                    <h3 class="point-title">獲得称号</h3>
                    <div class="point-info">
                        <table v-for="title in result.getTitle" v-if="result.getTitle.length != 0">
                            <tr>
                                <td>{{ title.name }}</td>
                                <td>+{{ title.point }}ml</td>
                            </tr>
                        </table>
                        <div v-else="result.getTitle.length != 0">
                            獲得した称号なし
                        </div>
                        <div>
                            合計称号リア充度: {{ totalTitlePoint }}ml
                        </div>
                    </div>
                </section>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: ['result'],
    computed: {
        totalBonusPoint() {
            if(this.result.getBonuses.length == 0) return 0
            return this.result.getBonuses.reduce((p, c) => p + parseInt(c.point), 0)
        },
        totalTitlePoint() {
            if(this.result.getTitle.length == 0) return 0
            return this.result.getTitle.reduce((p, c) => p + parseInt(c.point), 0)
        },
    }
}
</script>

<style scoped>
    h1 {
        margin: 0;
    }
    h3 {
        margin-bottom: 0;
    }
    .post-result {
        background-color: #fff;
        color: #1f1f21;
        text-align: left;
        padding: 20px;
    }
    .point-section {
        width: 100%;
        box-sizing: border-box;
        position: relative;
        text-align: center;
    }
    .point-title {
        margin: 0;
        text-align: center;
        background-color: #fff;
        z-index: 1;
        position: relative;
        display: inline-block;
    }
    .point-info {
        border: solid 1px;
        padding-top: 20px;
        margin-top: -20px;
        z-index: 0;
        position: relative;
    }
    .point-info table, .point-info tr {
        width: 100%;
    }
    .point-info td {
        width: 50%;
    }
</style>



